.submit-signin {
    border: 1px solid #bfff12;
	padding: 15px 25px;
	border-radius: 10px;
	background: transparent;
	color: #bfff12;
	width: 150px;
	text-transform: uppercase;
	transition: .2s background;
	font-size: 14px;
	cursor: pointer;
	margin-top: 30px;
}

div button:hover {
		color: #EF2917;
		background: #bfff12;
	}


    input {
		padding: 10px 20px;
		margin: 10px 0;
		border-radius: 10px;
		background: #ddd;
		outline: none;
		border: 1px solid transparent;
		transition: .2s border;
    }

   div input:focus{
       	border: 1px solid #EF2917;
		
   } 

   div input:active {
    border: 1px solid #EF2917;
   }

   div input:hover {
    border: 1px solid #EF2917;
